  font-style: normal;
  font-weight: 300;
  src: local("Roboto"), url("../../fonts/roboto/Roboto-Light.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../../fonts/roboto/Roboto-Regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto"), url("../../fonts/roboto/Roboto-Medium.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  src: local("Roboto"), url("../../fonts/roboto/Roboto-Bold.woff") format("woff");
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  line-height: 1.2;
}

.wrapper {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .wrapper {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media (min-width: 1024px) {
  .wrapper {
    padding-left: 60px;
    padding-right: 60px;
  }
}

@media (min-width: 1280px) {
  .wrapper {
    padding-left: calc( (100% - 1220px) / 2);
    padding-right: calc( (100% - 1220px) / 2);
  }
}

@media (min-width: 1600px) {
  .wrapper {
    padding-left: calc( (100% - 1540px) / 2);
    padding-right: calc( (100% - 1540px) / 2);
  }
}

.btn-row {
  padding-top: 200px;
  display: flex;
  justify-content: center;
}

.btn {
  font-size: 18px;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 4px;
}

.btn + .btn {
  margin-left: 20px;
}

.btn-blue {
  background-color: #5D71DD;
  border: none;
  color: white;
}

.btn-blue:focus {
  outline: none;
  box-shadow: 0 0 0 2pt rgba(93, 113, 221, 0.6);
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: visibility 0s ease-in-out, opacity .3s ease-in-out;
}

.modal.is-visible-modal {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal.is-centered-modal {
  display: flex;
  align-items: center;
}

.modal::before {
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.modal__container {
  position: relative;
  padding: 40px;
  width: calc(100% - 40px);
  margin: 50px auto;
  display: none;
  background-color: white;
  border-radius: 8px;
  cursor: default;
  text-align: right;
}

.modal__container.is-visible-modal {
  display: block;
}

@media (min-width: 768px) {
  .modal__container {
    width: calc(100% - 60px);
  }
}

@media (min-width: 1024px) {
  .modal__container {
    width: 800px;
  }
}

.modal__close {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.modal__close::before {
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0)'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.93 4.929a1 1 0 000 1.414L10.587 12 4.93 17.657a1 1 0 101.415 1.414l5.657-5.657 5.656 5.657a1 1 0 001.415-1.414L13.416 12l5.657-5.657a1 1 0 00-1.415-1.414l-5.657 5.657-5.656-5.657a1 1 0 00-1.415 0z' fill='%232A2E49'/%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip0'%3e%3cpath fill='%23fff' d='M0 0h24v24H0z'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: 50% 50%;
  background-color: transparent;
  background-size: contain;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.modal__title {
  font-size: 24px;
  text-align: center;
  font-weight: 600;
  margin-bottom: 30px;
}

.modal__text {
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 1.4;
  text-align: left;
}

.modal__text + .modal__text {
  margin-top: 18px;
}

.modal .btn {
  margin-top: 30px;
}
